export class PathHelper {
    public static join(path1: string, path2: string) {
        const pathPart1 = path1.endsWith("/") ? path1.substring(0, path1.length - 1) : path1;
        const pathPart2 = path2.startsWith("/") ? path2.substring(1) : path2;
        let fullPath = pathPart1 + "/" + pathPart2;
        fullPath = fullPath.endsWith("/") ? fullPath.substring(0, fullPath.length - 1) : fullPath;
        return fullPath;
    }
}